.home-grid {
    display: grid;
    grid-template-columns: minmax(500px, 1fr) minmax(550px, 1.2fr);
    grid-template-rows: minmax(200px, 1.2fr) minmax(200px, 1fr);
    width: auto;
    height: calc(100% - var(--footer-height) + 10px);
    margin: 20px 20px 0 60px;
    gap: 20px;
    overflow: visible;
}

.home-grid > div:nth-child(3) {
    grid-column: 1 / 3;
}

.flex-container-home {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.flex-row-home {
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: end;
    overflow: visible;
}

.flex-grid-home {
    display: grid;
    grid-template-rows: 25vh 65px 10vh;
}

.home-find-title {
    font-size: 9vh;
    color: var(--primary-color);
    padding: 0;
    justify-content: end;
    margin: 0 0 -2.2vh 20px;
}

.home-find-sub {
    font-size: 2.1vh;
    color: var(--primary-color);
    margin: 3px 0 0;
}

.shop-now-button {
    border-right-width: 0;
    border-left-width: 0;
    border-bottom-width: 0;
    border-radius: 8px;
    color: white;
    background-color: var(--primary-color);
    margin-top: 20px;
    padding: 20px 50px;
    font-size: 2vh;
    font-weight: bold;
    cursor: pointer;
}

.shop-now-button {
    border-top-width: 0;
}

.shop-now-button:hover {
    background-color: #a47175;
}

.shop-now-button:active {
    background-color: #593642;
    scale: 0.98;
}